.update{
    display: flex;
}

.container{
    flex: 4;
    font-family: 'Nunito', sans-serif;

}


.formcontainer{
    /* padding: 20px; */
    width: 500px;
    margin: 4rem auto;
    background-color: #FFFFFF;
    box-shadow: 5px 5px 10px lightgray;
}

.formheading{
    height: 60px;
    background-color: #E5F4F6;
    display: grid;
    place-content: center;
    font-weight: bold;
}
form{
    display: flex;
    flex-direction: column;
    gap: 20px;
    padding: 40px;
}

.formfield{
    display: flex;
    flex-direction: column;
}

input, select, button{
    padding: 5px;
    border: 1px solid rgb(228, 228, 228);
    border-radius: 10px;
}

input:hover{
    background-color: rgba(225, 222, 222, 0.32);
}
