.create {
  display: flex;
}

.container {
  flex: 4;
  display: flex;
  flex-direction: column;
  background-color: #dff6dd;
}

.createcontainer {
  width: 500px;
  margin: 40px auto;
  -webki-box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
  box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
  font-family: 'Roboto Mono', monospace;
  border-radius: 20px;
}

.bottom {
  /* -webki-box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
  box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47); */
  background-color: #f8f8f8;
  padding: 20px;
  display: flex;
  border-radius: 0 0 20px 20px ;
  /* font-weight: 600; */
  font-family: 'Nunito', sans-serif;

}

.top {
  padding: 20px;
  background-color: #000000;
  color: aliceblue;
  display: grid;
  place-content: center;
  border-radius: 20px 20px 0 0;
  font-family: 'Nunito', sans-serif;
}

.left {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}

.img {
  width: 200px;
  height: 200px;
  border-radius: 50%;
}

.right {
  flex: 2;
  display: flex;
  align-items: center;
  justify-content: center;
}

.form {
  gap: 20px;
  justify-content: space-around;
}

.formfield {
  display: flex;
  flex-direction: column;
}

.error{
  padding: 2px;
  color: rgb(1, 1, 1);
  background-color: rgba(239, 70, 70, 0.747);
  border-left: 5px solid red;
}
