
.sidebar {
  flex: 1;
  border-right: 0.5px solid rgb(222, 219, 219);
  min-height: 100vh;
  background-color: #262e40;
  /* position: fixed;
  z-index: 1; */
}

.top {
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 20px;
  color: aliceblue;
  font-family: 'Archivo Black', sans-serif;
  font-size: 20px;
}

.center {
  height: 200px;
}

.avtar {
  height: 70%;
  display: grid;
  place-content: center;
}

img {
  width: 120px;
  height: 120px;
  border-radius: 50%;
}

.title{
    padding: 20px;
    /* display: grid; */
    /* place-content: center; */
    color: aliceblue;
    font-weight: bold;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.bottom{
    /* padding: 10px; */
    display: flex;
    flex-direction: column;
}

.ul{
    list-style: none;
    color: aliceblue;
    background-color: #333d54;

}

.list{
    padding: 15px;
    margin: 10px 0;
    background-color: #262e40;
    padding-left: 40px;
    font-family: 'Nunito', sans-serif;

}
