
.login-container{
   --bg-image: url('../../images/axe.png');
   --bg-image-opacity: .8 ;

   background-image: var(--bg-image) ;
   background-size: cover;
   background-repeat: no-repeat;
    color: #e0e2e4;
    padding: 5px;
    position: relative;
    isolation: isolate;
}

.link{
    display: flex;
    align-items: center;
    color: #e0e2e4;
}

.mat{
    color: rgb(245, 240, 240) !important;
}

.login{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 20px;
    margin: 20px;
    font-family: 'Signika', sans-serif;
}

.form-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    height:68vh;
    gap: 20px;
}

h2{
    font-size: 2rem;
}

.form-fields{
    display: flex;
    flex-direction: column;
}

label{
    font-weight: 300;
}

.input{
    padding: 10px;
    border: none;
    border-radius: 8px;
    background-color: #e0e2e4;
    width: 250px;
}

.form-button{
    width: 250px;
    padding: 10px;
    font-weight: 600;
    background-color: rgb(140, 217, 155) !important;
}

.error{
    padding: 7px;
    background-color: #ed99906a;
    border-left: 5px solid red;
    font-weight: 300;
    display: flex;
    justify-content: space-between;
}

.material-symbols-outlined{
    color: black;
}

span{
    color: #e0e2e4;
}