.offers {
  margin: 40px;
}

.header {
  display: grid;
  place-content: center;
  height: 100px;
  background-color: #4158d0;
  background-image: linear-gradient(
    43deg,
    #4158d0 0%,
    #c850c0 46%,
    #ffcc70 100%
  );
  font-weight: bold;
  font-size: 2.5rem;
  color: aliceblue;
  font-family: "Roboto Mono", monospace;
}

.headercontent {
  padding: 20px;
  text-align: center;
  font-family: "Signika", sans-serif;
  font-size: 1.5rem;
}

.offercontainer {
  margin: 20px;
  display: grid;
  grid-template-columns: auto auto auto;
  justify-content: space-evenly;
  gap: 20px;
}

@media screen and (max-width: 600px) {
  .offercontainer {
    grid-template-columns: auto auto;
  }
  
}

.card {
    width: 25vw;
  -webki-box-shadow: 2px 4px 10px 5px rgba(201, 201, 201, 0.47);
  box-shadow: 2px 4px 10px 5px rgba(201, 201, 201, 0.47);
  height: 350px;
  display: flex;
  flex-direction: column;
  border-radius: 5px;
}

/* .card:hover{
    transform: scale(1.1);
  z-index: -1;
} */

.top{
    height: 15%;
    background-color: #3f3f3f;
    display: grid;
    place-content: center;
    color: aliceblue;
    font-weight: 900;
    font-family: 'Oswald', sans-serif;
}

.middle{
    height: 30%;
    background-color: #6f6f6f;
    display: grid;
    place-content: center;
}

.price{
    font-size: 1.5rem;
    font-weight: 600;
    color: aliceblue;
}

.expiry{
    color: aliceblue
}

.bottom{
    padding-top: 20px;
    height: 60%;
    background-color: aliceblue;
    display: flex;
    flex-direction: column;
    gap: 20px;
    text-align: center;
    color: #5f5f5f;
    font-family: 'Oswald', sans-serif;
}

.end{
    display: grid;
    place-content: center ;
    background-color: aliceblue;
    padding-bottom: 20px;
}

