  transform: scale(1.1);
} */

.card-1 {
  font-size: 20px;
}

.material-symbols-outlined {
  font-variation-settings: "FILL" 1, "wght" 600, "GRAD" 200, "opsz" 48;
}

.help-top{
    font-weight: 1.5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
}

.bottom{
    text-align: center;
}
