body{
}

.nav-container {
  min-width: 100%;
  height: 60px;
  background-image: url('../../images/background.jpg');
  color: white;
  position: sticky;
  top: 0;
}


.nav-items{
  padding: 20px;
  display: flex;
  justify-content: space-between;
  font-family: 'Roboto Mono', monospace;
  font-weight: bold;
  font-size: 20px;
}

.right{
    display: flex;
    gap: 20px;
}

.link:hover{
  padding: 0 2px;
  outline: 2px solid aliceblue;
  border-radius: 5px;
}

@media screen and (max-width: 600px) {
  .right{
    display: none;
  }
}

