.userlist {
  display: flex;
  font-family: "Nunito", sans-serif;
}

.usercontainer {
  flex: 4;
  display: flex;
  flex-direction: column;
}

.headerContainer {
  margin: 0 20px;
  display: flex;
  justify-content: space-between;
}

.search {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px;
}

.createbutton {
  padding: 5px;
  align-self: flex-end;
  background-color: #e5f5f7;
  border: 1px solid gray;
  border-radius: 5px;
}

.tableWrapper {
  max-width: 1000px;
  max-height: 400px;
  overflow-y: scroll;
  margin: 2px 20px;
  overflow-x: scroll;
  border: 2px solid black;
}

.table {
  border-collapse: collapse;
  width: 100%;
}

thead {
  position: sticky;
  top: 0;
  background-color: rgb(49, 154, 49);
  color: aliceblue;
}

td,
th {
  padding: 10px;
  border: 2px solid lightgray;
  text-align: center;
}

th {
text-align: center;
justify-content: center;
}

tbody {
  background-color: #eae6e6;
}

.actionsContainer {
  display: flex;
  gap: 5px;
}

.actions {
  cursor: pointer;
}

.paginationContainer {
  margin: 5px 20px;
  display: flex;
  justify-content: center;
  gap: 50px;
  align-items: center;
}

.input {
  border: 1px solid gray;
}

.remove {
  color: red;
}

.edit {
  color: green;
}

.tableheader {
  text-align: center;
}

.active {
  background-color: lightblue;
}
