.offers{
    display: flex;
    font-family: 'Nunito', sans-serif;
}

.offerscontainer{
    flex: 4;
}

.header{
    margin-right: 20px;
    display: flex;
    justify-content: flex-end;
}

.create{
    margin: 5px;
    padding: 5px;
    border-radius: 5px;
    background-color: #E5F5F7;
    border: 1px solid gray;
}

.tablecontainer{
    margin: 0 20px;
}

.tableheader{
    text-align: center;

}

.table{

    width: 100%;
    border-collapse: collapse;
}

.remove{
    color: red;
    cursor: pointer;
  }
  
  .edit{
    color: green;
    cursor: pointer;
  }