.policies {
  margin: 40px;
  display: grid;
  gap: 20px;
  color: #147bc5;
  font-weight: 900;
  font-family: "Roboto Mono", monospace;
}

.policies-container {
  display: grid;
  grid-template-columns: auto auto auto;
  justify-content: space-evenly;
  gap: 20px;
}

@media screen and (max-width: 600px) {
  .policies-container {
     display: flex;
     flex-direction: column;
  }

  .card{
    width: 100%;
  }
}

.header {
  width: 70%;
}

.card {
  height: 400px;
  padding: 25px;
  background-color: aliceblue;
  display: flex;
  flex-direction: column;
  gap: 20px;
  -webki-box-shadow: 2px 4px 10px 5px rgba(201, 201, 201, 0.47);
  box-shadow: 2px 4px 10px 5px rgba(201, 201, 201, 0.47);
}

/* .card:hover{
  transform: scale(1.1);
} */

.policies-text {
  font-weight: 300;
  margin-top: 1rem;
  color: #3c3f42;
}

.top {
  font-size: 2.5rem;
  font-weight: 900;
}

.bottom {
  font-size: 15px;
  font-weight: 300;
  color: #7e868d;
  display: flex;
  flex-direction: column;
  /* flex-wrap: wrap; */
  /* overflow: hidden; */
}

.button {
  margin-top: 15px;
  background-color: #3fa4f2;
  padding: 5px;
  border-radius: 7px;
  border: none;
}
